#click{
  display: none;
}

.button-badge.active{
  /* display: none; */
  border-radius: 50%;
  padding: 7px;
  width:5px;
  height: 5px;
  position: absolute;
  top: -3%;
  left: 65%;
  animation: mymove 1s infinite;
}

@keyframes mymove {

  0%   {background: red; left: 65%; top: -3%;}
}

.button-badge.none{
display: none;
}


