.chat-label{
  position: fixed;
  right: 30px;
  bottom: 20px;
  z-index: 200;

  height: 55px;
  width: 55px;
  background:#5887ef;
  text-align: center;
  line-height: 55px;
  border-radius: 50px;
  font-size: 30px;
  color: #fff;
  cursor: pointer;
}



.chat-label i{
  color: #fff !important;
  position: relative;
  top: 33%;
  left: 28%;
  transform: translate(-50%, -50%);
  transition: all 0.4s ease;
}

.chat-label i:hover{

color: #fff !important;
}


.material-icons{
  color: #5887ef !important;
}

.material-icons:hover{
color: #5887ef !important;
}

.chat-label i.fas{
  opacity: 0;
  pointer-events: none;
}
#click:checked ~ .chat-label i.fas{
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%, -50%) rotate(180deg);
}
/* #click:checked ~ .chat-label i.fab{
  opacity: 0;
  pointer-events: none;
  transform: translate(-50%, -50%) rotate(180deg);
} */

#click:checked ~ .chat-label i.fa{
  opacity: 0;
  pointer-events: none;
  transform: translate(-50%, -50%) rotate(180deg);
}

.chat-wrapper{
  position: fixed;
  right: 30px;
  bottom: 0px;
  z-index: 9999 !important;

  max-width: 400px;
  background: #fff;
  border-radius: 15px;
  box-shadow: 0px 15px 20px rgba(0,0,0,0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.6s cubic-bezier(0.68,-0.55,0.265,1.55);
}
#click:checked ~ .chat-wrapper{
  opacity: 1;
  bottom: 85px;
  pointer-events: auto;
}
.chat-wrapper .head-text{
  line-height: 60px;
  color: #fff;
  border-radius: 15px 15px 0 0;
  padding: 0 20px;
  font-weight: 500;
  font-size: 20px;
  background:#5887ef;
}
.chat-wrapper .chat-box{
  padding: 20px;
  width: 100%;
  height: 400px;
  /*min-width: 350px;*/
}
.chat-box .desc-text{
  color: #515365;
  text-align: center;
  line-height: 25px;
  font-size: 17px;
  font-weight: 500;

}

.chat-box form{
  padding: 2px 10px;
  margin: 20px 0;
  border-radius: 25px;
  border: 1px solid lightgrey;
  position: absolute;
  bottom:20px;
  left:4px;
}
.chat-box form .field{
  height: 50px;
  width: 100%;
  margin-top: 20px;

}
.chat-box form .field:last-child{
  margin-bottom: 15px;
}
form .field input,
form .field button,
form .textarea textarea{
  /* width: 100%; */
  height: 100%;
  padding-left: 20px;
  border: 1px solid lightgrey;
  outline: none;
  border-radius: 25px;
  font-size: 16px;
  transition: all 0.3s ease;
}
form .field input:focus,
form .textarea textarea:focus{
  border-color: #5887ef;
}
form .field input::placeholder,
form .textarea textarea::placeholder{
  color: silver;
  transition: all 0.3s ease;
}
form .field input:focus::placeholder,
form .textarea textarea:focus::placeholder{
  color: lightgrey;
}
.chat-box form .textarea{
  height: 70px;
  width: 100%;
}
.chat-box form .textarea textarea{
  height: 100%;
  border-radius: 50px;
  resize: none;
  padding: 15px 20px;
  font-size: 16px;
}
.chat-box form .field button{
  border: none;
  outline: none;
  cursor: pointer;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  background:none !important;
  transition: all 0.3s ease;
}

.chat-box form .field input{
/* width:60%; */
height:50px;
/* height: 100%; */
padding-left: 20px;
border: 1px solid lightgrey;
outline: none;
border-radius: 25px;
font-size: 16px;
transition: all 0.3s ease;
margin-left: 5px;
}



.chat-box form .field button:active{
  transform: scale(0.97);
}

#messages-box{
  margin: 0px !important;
  max-height: 250px;
  overflow-y: auto;
  overflow-x: hidden !important;
  max-width: 300px;
}


.user-message{
  display:block !important;
  margin-left: auto !important;
  text-align: left;
  max-width:200px;
  background: royalblue;
  padding:10px 20px;
  border-radius: 20px;
  margin-top: 10px;
  
  width:fit-content;   
  word-wrap:break-word;
  overflow-wrap:break-word;

}

.user-message span{
  color:#fff !important;
  display: inline-block;
  max-width:150px;

  /* margin-left: auto !important; */

}




.site-message{
  display:block !important;
  margin-right: auto !important;
  text-align: left;
  max-width:200px;
  background: #f2f4f6;
  padding:10px 20px;
  border-radius: 20px;
  margin-top: 10px;
  
  width:fit-content;   
}

.site-message span{
  color:#0d3f67;
  max-width:150px;

}

#user-message-div{
    padding-left:30px;
    margin-top: 20px;
}


#site-message-div{
    padding-right:30px;
}


#messages-box::-webkit-scrollbar{
  height: 4px;
  width: 4px;
}
#messages-box::-webkit-scrollbar-thumb:horizontal{
  border-radius: 10px;
}




