--------------------------------------------------------------*/
.buy-form{
  background: #f1cb2f;
}

.buy-form .info {
  width: 100%;
}

.buy-form .info i {
  font-size: 20px;
  color: #2487ce;
  float: left;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

.buy-form .info h4 {
  padding: 0 0 0 60px;
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 5px;
  color: #124265;
}

.buy-form .info p {
  padding: 0 0 0 60px;
  margin-bottom: 0;
  font-size: 14px;
  color: #217bbc;
}

.buy-form .info .email, .buy-form .info .phone {
  margin-top: 40px;
}

.buy-form .info .email:hover i, .buy-form .info .address:hover i, .buy-form .info .phone:hover i {
  background: #2487ce;
  color: #fff;
}

.buy-form .buy-paints {
  width: 100%;
}

.buy-form .buy-paints .form-group {
  padding-bottom: 8px;
}

.buy-form .buy-paints .error-message {
  display: none;
  color: #fff;
  background: #ed3c0d;
  text-align: left;
  padding: 15px;
  font-weight: 600;
}

.buy-form .buy-paints .error-message br + br {
  margin-top: 25px;
}

.buy-form .buy-paints .sent-message {
  display: none;
  color: #fff;
  background: #18d26e;
  text-align: center;
  padding: 15px;
  font-weight: 600;
}

.buy-form .buy-paints .loading {
  display: none;
  background: #fff;
  text-align: center;
  padding: 15px;
}

.buy-form .buy-paints .loading:before {
  content: "";
  display: inline-block;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  margin: 0 10px -6px 0;
  border: 3px solid #18d26e;
  border-top-color: #eee;
  -webkit-animation: animate-loading 1s linear infinite;
  animation: animate-loading 1s linear infinite;
}

.buy-form .buy-paints input {
  border-radius: 0;
  box-shadow: none;
  font-size: 14px;
  border-radius: 40px;
  text-align: center;
}

.buy-form .buy-paints textarea {
  border-radius: 0;
  box-shadow: none;
  font-size: 14px;
  border-radius: 30px;
  text-align: center;
}
.buy-form .buy-paints input:focus, .contact .buy-paints textarea:focus {
  border-color: #2487ce;
}

.buy-form .buy-paints input {
  height: 44px;
}

.buy-form .buy-paints textarea {
  padding: 10px 12px;
}

.buy-form .buy-paints button {
  background: #232e8a;
  border: 0;
  padding: 10px 30px;
  color: #fff;
  transition: 0.4s;
  border-radius: 30px;
  display: block;
  margin: auto;
  margin-top: 5%;
}

.buy-form .buy-paints button:hover {
  background: #fff;
  color:#232e8a;
}

@-webkit-keyframes animate-loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes animate-loading {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}






