.navbar2 a{
    justify-content: space-between;
    align-items: center;
    font-size: 13px;
    font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  color: #16507b;
  white-space: nowrap;
  transition: 0.3s;
}

.navbar2 a i {
    font-size: 12px;
    line-height: 0;
  }
  .navbar2 a:hover, .navbar2 .active, .navbar2 li:hover > a {
    color: #2487ce;
  }


  .navbar2 ul {
    padding-top: 1%;
    padding-left: 25%;
    display: flex;
    list-style: none;
    align-items: center;
  }
  .navbar2 li{
    position: relative;
  }

  .navbar3 ul {
    padding-top: 1%;
    padding-left: 25%;
    background-color: white;
    list-style: none;
    align-items: center;
  }


  @media (max-width: 1180px) {
  .navbar2 {
    margin-left: 25px;
  }
  }
  
  
  @media (max-width: 1130px) {
    .navbar2 {
      margin-left: 55px;
    }
    }
    
  
  @media (max-width: 991px) {
    .navbar2 {
      padding-left: 0px;
    }
    }
    #navbar2-logo{
        position: absolute;
        top:10px;
        left:5%;
        width: 200px;
      }
      @media (max-width: 991px) {
      #navbar2-logo{
        position:absolute;
        top:10px;
        left:5%;
        width:90px !important;
      }
      .navbar2 ul {
        display: inline;
        float: right;
    }
    
.navbar2 .dropdown ul a i {
    font-size: 12px;
  }
  
  .navbar2 .dropdown ul a:hover, .navbar2 .dropdown ul .active:hover, .navbar2 .dropdown ul li:hover > a {
    color: #2487ce;
  }
  
  .navbar2 .dropdown:hover > ul {
    opacity: 1;
    top: 100%;
    visibility: visible;
  }
  
  .navbar2 .dropdown .dropdown ul {
    top: 0;
    left: calc(100% - 30px);
    visibility: hidden;
  }
  
  .navbar2 .dropdown .dropdown:hover > ul {
    opacity: 1;
    top: 0;
    left: 100%;
    visibility: visible;
  }
  
  @media (max-width: 1366px) {
    .navbar2 .dropdown .dropdown ul {
      left: -90%;
    }
    .navbar2 .dropdown .dropdown:hover > ul {
      left: -100%;
    }
  }

    }

    .btnToggle{
      display: block;
      position: absolute;
      top: 55px;
      right: 15px;
      bottom: 15px;
      left: 15px;
      padding: 10px 0;
      background-color: #fff;
      overflow-y: auto;
      transition: 0.3s;
    }


    .navbar2-mobile {
      position: fixed;
      overflow: hidden;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      background: rgba(10, 38, 58, 0.9);
      transition: 0.3s;
      z-index: 999;
    }

    .navbar3-mobile {
      display: block;
      position: absolute;
      padding: 10px;
      overflow-y: auto;
      transition: 0.3s;
    }

    .navbar2-mobile .mobile-nav-toggle {
      position: absolute;
      top: 15px;
      right: 15px;
    }
    .navbar2-mobile ul {
      display: block;
      position: absolute;
   
      top: 55px;
      right: 15px;
      bottom: 15px;
      left: 15px;
      padding: 10px 0;
      background-color: #fff;
      overflow-y: auto;
      transition: 0.3s;
    }

    
.navbar2-mobile a {
  padding: 10px 20px;
  font-size: 15px;
  color: #124265;
}

.navbar2-mobile a:hover, .navbar2-mobile .active, .navbar2-mobile li:hover > a {
  color: #2487ce;
  
}

.navbar2-mobile .getstarted {
  margin: 15px;
}

